
// Large devices (desktops, 992px and up)
@media (min-width: 992px){
  .container{
    max-width: 992px;
    margin: 25px auto;
    -webkit-box-shadow: 2px -2px 10px 1px rgba(0,0,0,1);
    -moz-box-shadow: 2px -2px 10px 1px rgba(0,0,0,1);
    box-shadow: 2px -2px 10px 1px rgba(0,0,0,1);
  }

  .lg{
    &-small{
      font-size: 1.0rem;
      line-height: 1.0rem;
    }
    &-medium{
      font-size: 1.12rem;
      line-height: 1.7rem;
    }
    &-large{
      font-size: 2.2rem;
    }
  }

  section.main{
    overflow: hidden;
    &::before{
      content:'';
      background: url(../images/banner_min.jpg) no-repeat;
      background-size: 100%;
      position: absolute;
      top: 30px;
      left: 0px;
      width: 100%;
      height: 100%;
      z-index: -1;
    }
    & .header{
      position: relative;
      width: 100%;
      height: 70px;
      background-color: #fff;
      &::before{
        content:'';
        position: absolute;
        top: 20px;
        left: 50%;
        margin-left: -150px;
        background: url(../images/aspro_min.png) no-repeat;
        background-size: 100%;
        width: 300px;
        height: 100px;
        z-index: 3;
      }
    }
 
    & .triangles-upper-lower{
      &::before{
        content:'';
        position: absolute;
        top: 70px;
        right: 0px;
        border-right: 992px solid #fff;
        border-top: 0px solid transparent;
        border-bottom: 280px solid transparent;
        z-index: 2;
      }
      &::after{
        content:'';
        position: absolute;
        top: 390px;
        left: 0px;
        border-left: 992px solid #fff;
        border-top: 280px solid transparent;
        border-bottom: 0px solid transparent;
        z-index: 2;
      }
    }
  
    & .triangles-right-left{
      &::before{
        content:'';
        position: absolute;
        top: 70px;
        right: 0px;
        border-right: 470px solid #9a0007;
        border-top: 140px solid transparent;
        border-bottom: 130px solid transparent;
        z-index: 3;
      }
      &::after{
        content:'';
        position: absolute;
        top: 250px;
        left: 0px;
        border-left: 550px solid #272e38;
        border-top: 155px solid transparent;
        border-bottom: 340px solid transparent;
        z-index: 3;
      }
    }
  
    & .lines-long-short{
      &::before{
        content:'';
        position: absolute;
        right: 0;
        top: 590px;
        width: 620px;
        transform: skewY(16deg);
        border-bottom: 25px solid #9a0007;
        /* угол на конце линии*/
        border-left: 27px solid transparent;
        z-index: 2;
      }
      &::after{
        content:'';
        position: absolute;
        right: 0;
        top: 655px;
        width: 400px;
        transform: skewY(16deg);
        border-bottom: 18px solid #272e38;
        /* угол на конце линии*/
        border-left: 18px solid transparent;
        z-index: 2;
      }
    }
  
    & .contact{
      position: absolute;
      top: 360px;
      left: 40px;
      z-index: 4;
      &-company{
        color: #fff;
        font-size: 2.5rem;
        font-weight: bold;
        font-style: italic;
        margin-bottom: 0.001em;
      }
      &-address{
        color: #fff;
        font-size: 1.2rem;
        margin-bottom: 2.4em;
      }
      &-phone{
        color: #fff;
        font-size: 1.2rem;
        margin-bottom: 1em;
      }
      &-email{
        color: #fff;
        font-size: 1.2rem;
      }
      & .icon-phone::before{
        content: "";
        background: url(../images/phone.svg) no-repeat;
        background-size: 100%;
        padding-top: 24px;
        padding-left: 25px;
        margin-right: 18px;
      }
      & .icon-email::before{
        content: "";
        background: url(../images/email.svg) no-repeat;
        background-size: 100%;
        padding-top: 2px;
        padding-left: 25px;
        margin-right: 18px;
      }
      & a{
        color: #fff;
        &:hover{
          color: rgb(216, 208, 208);
        }
      }
    }

    & .menu{
      position: absolute;
      right: 65px;
      top: 145px;
      z-index: 100;
      &-item{
        font-weight: bold;
        font-size: 0.95rem;
        text-transform: uppercase;
        padding-bottom: 0.8rem;
      }
      & a{
        color: #fff;
        &:hover{
          cursor: pointer;
          color: #eee;
        }
      }
    }

    & .text{
      position: absolute;
      font-size: 1.1rem;
      top: 685px;
      left:0;
    }
 
  }
}